import { ADD_WIDGET, UPDATE_WIDGET, UPDATE_LAYOUT } from './actionTypes'

export function addWidget(content) {
  return {
    type: ADD_WIDGET,
    payload: {
      id: content.id,
      content
    }
  }
};

export function updateLayout(content) {
  return {
    type: UPDATE_LAYOUT,
    payload: {
      content
    }
  }
};